import { 
  LOG_IN_SUCCESS,
  LOG_IN_REQUEST,
  LOG_IN_FAILURE,
  LOG_OUT_SUCCESS,
  USER_DELETE,USER_SIGNUP,
  USER_EDIT,
  LOGIN_MODAL,
  PROFILE_IMAGE_EDIT,
  PROFILE_IMAGE_DELETE
} from "./types"

const loginInitialState = {
  isLogIn: false,
  data:[],
  SignUp: false,
  LoginModal: false,
  picture:null
}


const loginReducer = (state=loginInitialState, action) => {
  switch(action.type){
    case LOG_IN_SUCCESS:
      let dato = action.payload;
      console.log('reducerdddddddpost',dato)
      return {
        ...state, 
        data: action.payload,
        // 
        isLogIn: true
      }
      case LOG_IN_REQUEST:
        return {
          ...state
        }
      case LOG_IN_FAILURE:
        return {
          ...state, 
          data: data,
          isLogIn: true
        }
      case LOG_OUT_SUCCESS:
        return {
          ...state, 
          isLogIn: false,
          data:null
        }
      // 
      case USER_DELETE:
        return {
          ...state, 
          isLogIn: false,
          data: null
        }
      //
      case USER_SIGNUP:
        return {
          ...state, 
          SignUp: true
        }
       //
      case USER_EDIT:
        let data = action.payload
        console.log('reducerdddddddpost',data)
        return {
          ...state, 
          data: action.payload,
          isLogIn: true
        }   
      //     
      case LOGIN_MODAL:
        return {
          ...state, 
          LoginModal: true
        } 
      // 
      case PROFILE_IMAGE_EDIT:
        return {
          ...state, 
          picture: action.payload
        }
      // 
      case PROFILE_IMAGE_DELETE:
        return {
          ...state, 
          picture: null
        }               
    default: return state;
  }
}

export default loginReducer;