const loginSuccess = (data) => {
  // console.log('',data)
  //------------------data    
  return {
    type : LOG_IN_SUCCESS,
    payload: data
  }
}

const loginRequest = () => {
  // console.log('logindata',data)
  return {
    type : LOG_IN_REQUEST
  }
}

const loginFailure = (err) => {
  // console.log('logindata',data)
  return {
    type : LOG_IN_FAILURE,
    payload: err
  }
}

const logoutSuccess = () => {
  return {
    type : LOG_OUT_SUCCESS,
  }
}
